export default class VideosModel {
  constructor(state) {
    this.state = state;
  }

  static resizeTitle(s) {
    let newS = s;
    if (newS.length > 60) {
      newS = `${s.substring(0, 58)}...`;
    }
    return newS;
  }

  static resizeAuthor(s) {
    let newS = s;
    if (newS.length > 18) {
      newS = `${s.substring(0, 16)}...`;
    }
    return newS;
  }

  static async getViews(idArray, state) {
    const {
      secondLink, key,
    } = state;

    const url = `${secondLink}&key=${key}&id=${idArray.join(',')}`;
    const responce = await fetch(url);
    const data = await responce.json();
    fetch(url)
      .then(res => res.json());
    return data;
  }

  async getVideos() {
    let url = null;
    const {
      link, key, q, nextPageToken,
    } = this.state;
    if (nextPageToken === 0) {
      return;
    }
    if (!nextPageToken) {
      url = `${link}&key=${key}&q=${q}`;
    } else {
      url = `${link}&key=${key}&q=${q}&pageToken=${nextPageToken}`;
    }
    const responce = await fetch(url);
    const data = await responce.json();
    fetch(url)
      .then(res => res.json());
    this.state.numberOfVideos += data.items.length;
    const idArray = [];
    let newArray = [];
    let viewsArray = [];
    if (data.nextPageToken) {
      this.state.nextPageToken = data.nextPageToken;
    } else {
      this.state.nextPageToken = 0;
    }
    newArray = data.items.map((video) => {
      const obj = {};
      obj.title = VideosModel.resizeTitle(video.snippet.title);
      obj.imgUrl = video.snippet.thumbnails.medium.url;
      obj.author = VideosModel.resizeAuthor(video.snippet.channelTitle);
      obj.description = video.snippet.description;
      obj.date = video.snippet.publishedAt.substring(0, 10);
      obj.id = video.id.videoId;
      obj.nextPageToken = this.state.nextPageToken;
      obj.numberOfVideos = this.state.numberOfVideos;
      idArray.push(obj.id);
      return obj;
    });
    viewsArray = await VideosModel.getViews(idArray, this.state);
    for (let i = 0; i < newArray.length; i += 1) {
      newArray[i].views = viewsArray.items[i].statistics.viewCount;
    }
    // eslint-disable-next-line consistent-return
    return newArray;
  }
}
