export default class PaginationController {
  constructor() {
    this.info = {
      current: 1,
      prev: 0,
      first: 0,
      next: 2,
    };
  }

  nextClick() {
    this.info.current += 1;
    this.info.next += 1;
    if (this.info.prev === 0) {
      this.info.prev = 1;
    } else {
      this.info.prev += 1;
    }
    if (this.info.prev >= 2) {
      this.info.first = 1;
    }
  }

  prevClick() {
    if (this.info.current > 1) {
      this.info.current -= 1;
    }
    if (this.info.current <= 2) {
      this.info.first = 0;
    }
    this.info.next = this.info.current + 1;
    if (this.info.prev >= 1) {
      this.info.prev -= 1;
    }
  }

  firstClick() {
    this.info.current = 1;
    this.info.next = 2;
    this.info.prev = 0;
    this.info.first = 0;
  }

  setCurrentPage(current) {
    this.info.current = current;
    this.info.next = current + 1;
    this.info.prev = current - 1;
    if (current >= 3) {
      this.info.first = 1;
    }
  }

  offNextPage() {
    this.info.next = 0;
  }
}
