import VideosModel from '../models/VideosModel';
import PageView from '../views/PageView/PageView';
import PaginationController from './PaginationController';

export default class SearchBoxController {
  constructor() {
    this.state = {
      key: 'AIzaSyBYS_qf5grM9nglBx06-Ybhs7gQl4jzqjk',
      q: '',
      link: 'https://www.googleapis.com/youtube/v3/search?type=video&part=snippet&maxResults=15',
      secondLink: 'https://www.googleapis.com/youtube/v3/videos?part=snippet,statistics',
      nextPageToken: '',
      numberOfVideos: 0,
    };
  }

  resetState() {
    this.state.nextPageToken = '';
    this.state.numberOfVideos = 0;
  }

  static addPopupPaginationEvenets() {
    const buttons = document.querySelectorAll('.pagination button');
    function onMouseDown() {
      this.children[0].style.opacity = '1';
      this.children[0].style.display = 'block';
    }
    function onMouseUp() {
      this.children[0].style.opacity = '0';
      this.children[0].style.display = 'none';
    }
    Array.prototype.map.call(buttons, (button => button.addEventListener('mousedown', onMouseDown)));
    Array.prototype.map.call(buttons, (button => button.addEventListener('mouseup', onMouseUp)));
  }

  static addResizeEvents(pagination, view, state) {
    const itemsAreaElement = document.querySelector('ul');
    let oldN = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--n'), 10);
    function onResize() {
      let oldI = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--i'), 10);
      const n = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--n'), 10);
      let i = 0;
      if (n !== oldN) {
        if (n > oldN) {
          const videoNumberToSave = oldI * oldN;
          i = Math.floor(videoNumberToSave / n);
          itemsAreaElement.style.setProperty('--i', i);
          pagination.setCurrentPage(i + 1);
          if (!SearchBoxController.checkForUlEnd(state, i - 1)) {
            pagination.offNextPage();
          }
          view.setUpdatedPagination(pagination);
        }
        if (n < oldN) {
          const videoNumberToSave = oldI * oldN;
          i = Math.round(videoNumberToSave / n);
          itemsAreaElement.style.setProperty('--i', i);
          pagination.setCurrentPage(i + 1);
          if (!SearchBoxController.checkForUlEnd(state, i)) {
            pagination.offNextPage();
          }
          view.setUpdatedPagination(pagination);
        }
        oldN = n;
        oldI = i;
      }
    }

    window.addEventListener('resize', onResize);
  }

  static addPaginationEvents(pagination, view, state) {
    const itemsAreaElement = document.querySelector('ul');
    let isVideosEnd = false;
    const onNextClickButtonEvent = function n() {
      isVideosEnd = SearchBoxController.checkForVideosEnd(state);
      let currentVar = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--i'), 10);
      if (!SearchBoxController.checkForUlEnd(state, currentVar) && !isVideosEnd) {
        pagination.nextClick();
        pagination.offNextPage();
      } else {
        pagination.nextClick();
      }
      view.setUpdatedPagination(pagination);
      itemsAreaElement.style.setProperty('--i', currentVar += 1);
    };
    const onPrevClickButtonEvent = function p() {
      let currentVar = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--i'), 10);
      pagination.prevClick();
      view.setUpdatedPagination(pagination);
      itemsAreaElement.style.setProperty('--i', currentVar -= 1);
    };
    const onFirstClickButtonEvent = function f() {
      pagination.firstClick();
      view.setUpdatedPagination(pagination);
      itemsAreaElement.style.setProperty('--i', 0);
    };
    const firstPage = document.getElementById('firstPage');
    const prevPage = document.getElementById('prevPage');
    const nextPage = document.getElementById('nextPage');
    nextPage.addEventListener('click', onNextClickButtonEvent);
    prevPage.addEventListener('click', onPrevClickButtonEvent);
    firstPage.addEventListener('click', onFirstClickButtonEvent);
  }

  static addMotionEvents(pagination, view, state) {
    const itemsAreaElement = document.querySelector('ul');
    const itemsAreaDiv = document.querySelector('.items-area');
    itemsAreaElement.classList.add('smooth');
    let x0 = null;
    let i;
    let f = 3;
    let draggable = false;
    const areaWidth = parseInt(getComputedStyle(itemsAreaElement).width, 10);
    let isVideosEnd = false;

    function unify(e) {
      return e.changedTouches ? e.changedTouches[0] : e;
    }
    function onLock(e) {
      x0 = unify(e).clientX;
      i = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--i'), 10);
      draggable = true;
      itemsAreaElement.classList.remove('smooth');
      isVideosEnd = SearchBoxController.checkForVideosEnd(state);
      f = 3;
      itemsAreaDiv.style.cursor = 'grabbing';
    }

    function onMove(e) {
      if (x0) {
        const dx = unify(e).clientX - x0;
        const dxSign = Math.sign(dx);
        itemsAreaElement.style.setProperty('--tx', 0);
        f = +(dxSign * dx / areaWidth).toFixed(2);
        f = 1 - f + 1;
        if (areaWidth / 5 < dx * dxSign) {
          if (dxSign < 0) {
            if (pagination.info.next !== 0) {
              pagination.nextClick();
              view.setUpdatedPagination(pagination);
              itemsAreaElement.style.setProperty('--i', i += 1);
            }
          }
          if (dxSign > 0) {
            if (i > 0) {
              itemsAreaElement.style.setProperty('--i', i -= 1);
              pagination.prevClick();
              view.setUpdatedPagination(pagination);
            }
          }
        }
      }
      itemsAreaDiv.style.cursor = 'grab';
      itemsAreaElement.classList.add('smooth');
      itemsAreaElement.style.setProperty('--f', f);
      itemsAreaElement.style.setProperty('--tx', '0px');
      const newI = i - 1;
      if (!SearchBoxController.checkForUlEnd(state, newI) && !isVideosEnd && draggable) {
        pagination.offNextPage();
        view.setUpdatedPagination(pagination);
      }
      x0 = null;
      isVideosEnd = false;
      draggable = false;
    }
    function onDrag(e) {
      e.preventDefault();
      if (draggable) {
        if (x0) {
          itemsAreaElement.style.setProperty('--tx', `${Math.round(unify(e).clientX - x0)}px`);
        }
      }
    }
    itemsAreaDiv.addEventListener('mousedown', onLock, false);
    itemsAreaDiv.addEventListener('touchstart', onLock, false);
    itemsAreaElement.addEventListener('mousemove', onDrag, false);
    itemsAreaElement.addEventListener('touchmove', onDrag, false);
    document.body.addEventListener('mouseup', onMove, false);
    document.body.addEventListener('touchend', onMove, false);
  }

  static checkForVideosEnd(state) {
    const { numberOfVideos } = state;
    const itemsAreaElement = document.querySelector('ul');
    const i = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--i'), 10);
    const n = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--n'), 10);
    if (((i + 1) * n + n) > numberOfVideos - n && state.nextPageToken) {
      SearchBoxController.prototype.generateNextTokenVideos(state);
      return true;
    }
    return false;
  }

  static checkForUlEnd(state, i) {
    const { numberOfVideos } = state;
    if (numberOfVideos !== 0) {
      const itemsAreaElement = document.querySelector('ul');
      const n = parseInt(getComputedStyle(itemsAreaElement).getPropertyValue('--n'), 10);
      if (((i + 1) * n) >= numberOfVideos - n || numberOfVideos === 0) {
        return false;
      }
    }
    return true;
  }

  async generateVideos() {
    if (document.querySelector('.pagination')) {
      PageView.removeVideos();
      PageView.removePagination();
      this.resetState();
    }
    const model = new VideosModel(this.state);
    const data = await model.getVideos();
    const pagination = new PaginationController();
    const view = new PageView(data, pagination);
    view.show(this.state);
    if (!SearchBoxController.checkForUlEnd(this.state, 0)) {
      pagination.offNextPage();
    }
    view.setPagination();
    if (data.length > 0) {
      SearchBoxController.addResizeEvents(pagination, view, this.state);
      SearchBoxController.addPaginationEvents(pagination, view, this.state);
      SearchBoxController.addMotionEvents(pagination, view, this.state);
      SearchBoxController.addPopupPaginationEvenets();
    }
  }

  async generateNextTokenVideos(state) {
    this.state = state;
    const model = new VideosModel(this.state);
    const data = await model.getVideos();
    PageView.addShow(data);
  }

  init() {
    document.body.innerHTML = '<form class="search" onsubmit ="return onFormSubmit()"><label for="from"><i class="fas fa-search"></i></label><input type="text" class="searchTerm" placeholder="Type..."><input type="submit" class="searchButton" value="Search"></form><div class="items-area"></div>';
    const that = this;

    function onFormSubmit() {
      const input = document.getElementsByClassName('searchTerm')[0];
      if (input.value.length !== 0) {
        that.state.q = input.value;
        that.generateVideos();
        return false;
      }
      return false;
    }
    document.querySelector('.search').onsubmit = onFormSubmit;
  }
}
