import './page-view.css';

export default class PageView {
  constructor(videos, pagination) {
    this.videos = videos;
    this.pagination = pagination;
  }

  static removeVideos() {
    const itemsArea = document.querySelector('.items-area');
    let child = itemsArea.lastElementChild;
    if (!child) {
      return;
    }
    while (child) {
      itemsArea.removeChild(child);
      child = itemsArea.lastElementChild;
    }
  }

  static removePagination() {
    document.body.removeChild(document.body.children[document.body.children.length - 1]);
  }

  show(state) {
    if (this.videos.length === 0) {
      const content = document.createElement('div');
      content.innerHTML = `No results found for <b>${state.q}</b>`;
      document.querySelector('.items-area').appendChild(content);
      document.querySelector('.items-area').classList.remove('grab-cursor');
      document.querySelector('.items-area').style.cursor = 'auto';
    } else {
      const content = document.createElement('ul');
      content.innerHTML = this.videos.map(video => `<li><div class="item-title"><a href="http://youtube.com/watch?v=${video.id}"><span>${video.title}</span></a></div><div class="item-image"><img src="${video.imgUrl}"></div><div class="item-others"><div class="item-author">${video.author}</div><div class="item-date">${video.date}</div><div class="item-views">${video.views}</div><div class="item-description">${video.description}</div></div></li>`).join('');
      document.querySelector('.items-area').appendChild(content);
      document.querySelector('.items-area').classList.add('grab-cursor');
    }
  }

  static addShow(newVideos) {
    if (newVideos) {
      const content = document.querySelector('ul');
      newVideos.map((video) => {
        const contentLi = document.createElement('li');
        contentLi.innerHTML = `<div class="item-title"><a href="http://youtube.com/watch?v=${video.id}"><span>${video.title}</span></a></div><div class="item-image"><img src="${video.imgUrl}"></div><div class="item-others"><div class="item-author">${video.author}</div><div class="item-date">${video.date}</div><div class="item-views">${video.views}</div><div class="item-description">${video.description}</div></div>`;
        return content.appendChild(contentLi);
      });
    }
  }

  setPagination() {
    const {
      first, prev, current, next,
    } = this.pagination.info;
    const paginationDiv = document.createElement('div');
    paginationDiv.setAttribute('class', 'pagination');
    paginationDiv.innerHTML = `<button type="button" id="firstPage" class="btn"><span class="span-popup">${first}</span><span><i class="fas fa-chevron-up"></i></span></button><button type="button" id="prevPage" class="btn"><span class="span-popup">${prev}</span><span><i class="fas fa-chevron-left"></i></span></button><div id="currentPage" class="current"><span>${current}</span></div><button type="button" id="nextPage" class="btn"><span class="span-popup">${next}</span><span><i class="fas fa-chevron-right"></i></span></button>`;
    document.body.appendChild(paginationDiv);
    const firstPage = document.getElementById('firstPage');
    const prevPage = document.getElementById('prevPage');
    const nextPage = document.getElementById('nextPage');
    firstPage.classList.add('not-to-display-first');
    prevPage.classList.add('not-to-display');
    if (next === 0) {
      nextPage.classList.add('not-to-display');
    }
  }

  setUpdatedPagination(newPaginationObj) {
    this.pagination = newPaginationObj;
    const {
      first, prev, current, next,
    } = this.pagination.info;
    const firstPage = document.getElementById('firstPage');
    const currentPage = document.getElementById('currentPage');
    const prevPage = document.getElementById('prevPage');
    const nextPage = document.getElementById('nextPage');
    if (current > 2) {
      firstPage.classList.remove('not-to-display-first');
    }
    if (current < 3) {
      firstPage.classList.add('not-to-display-first');
    }
    if (current > 1) {
      prevPage.classList.remove('not-to-display');
    }
    if (current < 2) {
      prevPage.classList.add('not-to-display');
    }
    if (next === 0) {
      nextPage.classList.add('not-to-display');
    }
    if (next !== 0) {
      nextPage.classList.remove('not-to-display');
    }
    currentPage.children[0].innerHTML = current;
    firstPage.children[0].innerHTML = first;
    nextPage.children[0].innerHTML = next;
    prevPage.children[0].innerHTML = prev;
  }
}
